<script setup>
import { ref, reactive } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { useI18n } from 'vue-i18n';
import { commonStore } from '@/store/common';
import { NPopselect } from 'naive-ui';
const route = useRoute();
const router = useRouter();
const { locale, t } = useI18n();
const common_store = commonStore();
defineProps({
  scrollTop: Number,
});
// const mangerAddr = '0x22a64de2f812a0da64142b5a2dc13847f98c33e4';
const mangerAddr = '0x9c12D3C1CB846717ed7cd2AA890BaC331264b236';
const data = reactive({
  index: 0,
  path: '/',
});
const navArr = [
  {
    title: 'Nav.home',
    link: '/',
    requireAuth: false,
  },
  {
    title: 'Nav.market',
    link: '/market',
    requireAuth: false,
  },
  {
    title: 'Nav.nft',
    link: '/nft',
    requireAuth: true,
  },
  {
    title: 'Nav.exchange',
    link: '/exchange',
    requireAuth: true,
  },
  {
    title: 'Nav.account',
    link: '/account',
    requireAuth: true,
  },
  {
    title: 'Nav.pledge',
    link: 'no',
    icon: require('@assets/commonImg/icon11.png'),
    requireAuth: false,
  },
];
const langOption = [
  {
    label: 'English',
    value: 'EN',
  },
  {
    label: '',
    value: 'CN',
  },
  {
    value: 'JA',
    label: '',
  },
];
const setInfo = {
  link: '/set',
  requireAuth: true,
};
const handleSelect = (key) => {
  //   locale.value = key;
  //   localStorage.setItem('locale', key);
  console.log(key);
};
const toLink = () => {
  if (common_store.address) {
    common_store.setAccountShow(true);
    return;
  }
  common_store.setLinkShow(true);
};
const toDetails = (obj) => {
  if (obj.link == 'no') {
    window.$message.warning(t('notIn'));
    return;
  }
  if (obj.requireAuth && !common_store.address) {
    common_store.setLinkShow(true);
    return;
  }
  if (route.path == obj.link) {
    return;
  }
  router.replace(obj.link);
};
</script>
<template>
  <div
    class="header"
    :class="
      scrollTop < 180 && route.name == 'home' ? 'transparentBg' : 'mainBg'
    "
  >
    <div class="cen header-main flex_between">
      <ul class="flex_start header_left">
        <li class="flex_start">
          <img src="@assets/commonImg/logo.png" alt="" class="logo_icon" />
        </li>
        <li
          v-for="(item, key) in navArr"
          :key="item.title"
          @click="toDetails(item, key)"
          :class="[route.path == item.link ? ' active_btn' : '']"
          class="li centerFlex"
        >
          <p class="font-Six Wcolor hand">
            {{ $t(item.title) }}
          </p>
        </li>
        <li
          :class="[route.path == '/set' ? ' active_btn' : '']"
          class="li centerFlex"
          @click="toDetails(setInfo)"
          v-if="
            common_store.address &&
            common_store.address.toLowerCase() == mangerAddr.toLowerCase()
          "
        >
          <p class="font-Six Wcolor hand"></p>
        </li>
      </ul>
      <div class="flex_start header_right">
        <n-popselect
          trigger="click"
          @select="handleSelect"
          :options="langOption"
          v-model:value="locale"
        >
          <div class="lang_icon flex_start">
            <img src="@assets/commonImg/lang-icon.png" alt="" class="hand" />
          </div>
        </n-popselect>

        <n-button type="primary" class="right_btn" @click="toLink">{{
          common_store.address
            ? common_store.address.slice(0, 6) +
              '****' +
              common_store.address.slice(-4)
            : $t('Nav.connect')
        }}</n-button>
      </div>
    </div>
  </div>
</template>

