  height: 70px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1999;
  background: $headerBg;
  transition: background ease-in 0.3s;
  &-main {
    height: 70px;
  }
  &_left {
    .li {
      height: 40px;
      line-height: 40px;
      margin-left: 50px;
      transition: ease-in 0.3s;
      position: relative;
      transition: ease-in-out 0.3s;
      &:hover {
        transform: scale(1.3);
      }
    }
    .active_btn {
      p {
        animation: activeBtn 0.5s;
      }
      &::after {
        content: '';
        display: inline-block;
        width: 100%;
        height: 2px;
        border-radius: 4px;
        background: $white;
        position: absolute;
        bottom: 0px;
        left: 50%;
        transform: translate(-50%, 0);
        animation: activeBorder 0.3s;
      }
    }
  }
  &_right {
    height: 70px;
    .right_btn {
      margin-left: 30px;
    }
  }
}
