<template>
  <n-config-provider
    :locale="localI18n.lang"
    :theme="darkTheme"
    :theme-overrides="themeOverrides"
  >
    <n-message-provider>
      <message />
    </n-message-provider>
    <n-dialog-provider>
      <!-- <dialog-box /> -->

      <div id="app" class="app mainBg" @scroll="scrollEvent">
        <common-header :scrollTop="scrollTop"></common-header>
        <router-view class="view mainBg" v-if="isRouterAlive"></router-view>
        <common-footer></common-footer>
      </div>
      <loading v-show="loadingShow"></loading>
      <n-modal v-model:show="store.linkShow">
        <empower-address></empower-address>
      </n-modal>
      <!-- <van-popup v-model:show="empowerShow" :close-on-click-overlay="false">
        <empower></empower>
      </van-popup> -->
      <n-modal v-model:show="accountShow">
        <account-info></account-info>
      </n-modal>
      <n-modal v-model:show="store.likeShow">
        <propaganda-alert></propaganda-alert>
      </n-modal>
    </n-dialog-provider>
  </n-config-provider>
</template>

<script setup>
import { NConfigProvider, zhTW, enUS, NModal } from 'naive-ui';
import { useRouter, useRoute } from 'vue-router';
import empowerAddress from './components/empowerAddress.vue';
import accountInfo from './components/accountInfoAlert.vue';
import loading from './components/loading.vue';
import message from './components/message.vue';
import { computed, onBeforeMount, provide, ref, nextTick, watch } from 'vue';
import commonHeader from './components/commonHeader.vue';
import commonFooter from './components/commonFooter.vue';
import { useI18n } from 'vue-i18n';
import { darkTheme } from 'naive-ui';
import { commonStore } from '@/store/common';
import { swapStore } from '@/store/swap';
import propagandaAlert from './components/propagandaAlert.vue';

const router = useRouter();
const route = useRoute();
const store = commonStore();
const swap_store = swapStore();
const { locale } = useI18n();
const isRouterAlive = ref(true);
const scrollTop = ref(0);
const loadingShow = computed(() => store.loadingShow);
const accountShow = computed(() => store.accountShow);
const empowerShow = computed(() => store.empowerShow);
const reload = () => {
  isRouterAlive.value = false;
  nextTick(() => {
    isRouterAlive.value = true;
  });
};
provide('reload', reload);
const langArr = [
  {
    locale: 'CN',
    lang: zhTW,
  },
  {
    locale: 'EN',
    lang: enUS,
  },
];
const localI18n = computed(() => langArr.find((item) => item.locale == 'CN'));
const getInviteCode = (num) => {
  let url = num.slice(num.lastIndexOf('?') + 1); //
  let arr = url.split('&');
  let obj = {};
  if (arr.length > 0) {
    arr.forEach((item, key) => {
      //
      const params = item.split('=')[1];
      const name = item.split('=')[0];
      obj[name] = params;
    });
  }
  //
  if (obj.code) {
    sessionStorage.setItem('parent', obj.code);
  }
};
const scrollEvent = () => {
  scrollTop.value = window.pageYOffset;
};
watch(
  () => store.likeShow,
  (val) => {
    if (!val) {
      sessionStorage.setItem('likeShow', true);
    }
  }
);

onBeforeMount(() => {
  getInviteCode(window.location.href);
  if (store.address && store.walletType == 'wallet') {
    store.initWeb3();
    store.initMarketWeb3();
    swap_store.getTokenMethods();
  }
  if (store.address && store.walletType == 'walletConnect') {
    store.walletConnect();
    store.initMarketWeb3();
    swap_store.getTokenMethods();
  }
  if (store.address && store.walletType == 'BinanceChain') {
    store.initBSCWeb3();
    store.initMarketWeb3();
    swap_store.getTokenMethods();
  }
});
const themeOverrides = {
  common: {
    primaryColor: '#387DE2',
    primaryColorHover: '#387DE2',
    successColor: '#1DDAAB',
    successColorHover: '#2fe4b6',
    errorColor: '#e92023',
    errorColorHover: '#f33437',
    textColor: '#fff',
    inputColor: '#31373F',
  },
  Dropdown: {
    optionTextColor: '#999999',
    optionColorActive: '#3092FC',
    optionTextColorActive: '#3092FC',
  },
  Breadcrumb: {
    itemTextColor: '#fff',
    itemTextColorHover: '#3092FC',
    itemTextColorActive: '#3092FC',
  },
  Button: {
    textColor: '#fff',
  },
  DataTable: {
    borderColor: '#31373F',
    tdColor: '#2e3843',
    // tdColorStriped: '#242B33',
    tdColorHover: '#242B33',
    thColor: '#242B33',
    thColorHover: '#2e3843',
  },
};
</script>

