<script setup>
import { ref, reactive, computed } from 'vue';
import { useI18n } from 'vue-i18n';
import $ from 'jquery';
import { commonStore } from '@/store/common';
const common_store = commonStore();
const address = computed(() => common_store.address);
let show = ref(false);
const { locale, t } = useI18n();
const addAddress = () => {
  let addressArr = JSON.parse(localStorage.getItem('addressArr')) || [];
  addressArr.push(address.value);
  localStorage.setItem('addressArr', JSON.stringify(addressArr));
};
const getAddress = () => {
  let addressArr = JSON.parse(localStorage.getItem('addressArr')) || [];
  return addressArr.every((item) => item !== address.value);
};
const updata = () => {
  if (!address.value) {
    common_store.setLinkShow(true);
    return;
  }

  if (!getAddress()) {
    window.$message.warning(t('loveText'));
    return;
  }
  if (show.value) {
    return;
  }
  $.ajax({
    url: 'https://formspree.io/f/mnqwryoo',
    method: 'post',
    async: true,
    data: {
      address: address.value,
    },
    dataType: 'json',
    success: (data) => {
      if (data.ok) {
        addAddress();
      }
    },
  });
  show.value = true;
  const timer = setTimeout(() => {
    show.value = false;
    clearTimeout(timer);
  }, 1000);
};
const toHide = () => {
  common_store.likeShow = false;
  sessionStorage.setItem('likeShow', true);
};
</script>
<template>
  <ul class="propaganda listBg">
    <li>
      <p class="Wcolor font-Tf font500">{{ $t('Home.text6') }}</p>
      <p class="Wcolor font-Tf font500">{{ $t('Home.text7') }}</p>
    </li>
    <li class="propaganda-banner">
      <img src="@assets/propagandaImg/banner.png" alt="" />
    </li>
    <li class="text font-Two Wcolor">
      {{ $t('Propaganda.text1') }}
    </li>
    <li class="text font-Two Wcolor">
      {{ $t('Propaganda.text2') }}
    </li>
    <li class="btn">
      <n-button type="default" block @click="updata">{{
        $t('Home.likes')
      }}</n-button>
    </li>
    <li class="btn">
      <n-button
        type="primary"
        block
        class="font-Seven enter font600"
        @click="
          common_store.likeShow = false;
          sessionStorage.setItem('likeShow', true);
        "
        >{{ $t('enter') }}</n-button
      >
    </li>
    <li class="close_icon" @click="toHide">
      <img src="@assets/commonImg/close_icon.png" alt="" class="hand" />
    </li>
  </ul>
</template>

